<template>

  <div>
      {{pointGPS}}
      <button type="button" @click="cek" class="btn btn-sm btn-primary  ">cek</button>
    <l-map
      :zoom="zoom"
      :center="center"
      style="height: 500px; width: 100%"
    >
      <l-tile-layer
        :url="url"
        :attribution="attribution"
      />
      <l-circle
        :lat-lng="circle.center"
        :radius="circle.radius"
      />
      <l-marker :lat-lng="pointTempat">
          <l-icon
          :icon-size="dynamicSize" 
          :icon-anchor="dynamicAnchor"
          icon-url="https://www.iconpacks.net/icons/1/free-pin-icon-48-thumb.png"
        />
        <l-popup>
          <div @click="innerClick">
            I am a popup
          </div>
        </l-popup>
      </l-marker>
    <l-marker :lat-lng="pointGPS">
          <l-icon
          :icon-size="dynamicSize" 
          :icon-anchor="dynamicAnchor"
          icon-url="https://www.iconpacks.net/icons/1/free-pin-icon-48-thumb.png"
        />
        <l-popup>
          <div @click="innerClick">
            I am a popup
          </div>
        </l-popup>
      </l-marker>
    </l-map>
  </div>
</template>

<script>
import { latLng } from "leaflet";
import {
  LMap,
  LTileLayer,
  LCircle,
   LMarker,
    LPopup,
    LTooltip,
    LFeatureGroup,
    LIcon
} from "vue2-leaflet";

export default {
  name: "GeometryTest",
  components: {
    LMap,
    LTileLayer,
    LCircle,
     LMarker,
    LPopup,
    LTooltip,
    LFeatureGroup,
    LIcon
  },
  data() {
    return {
      zoom: 17,
      center: [-2.3614227630765185, 115.46969742626823],
      circle: {
        center: latLng(-2.3614227630765185, 115.46969742626823),
        radius: 400
      },
      pointTempat: latLng(-2.3614227630765185, 115.46969742626823),
      pointGPS:    latLng(-2.3624827630765185, 115.46969742626823),
      iconSize: 30,
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    };
  },
  computed: {
    dynamicSize() {
      return [this.iconSize, this.iconSize * 1.15];
    },
    dynamicAnchor() {
      return [this.iconSize / 2, this.iconSize * 1.15];
    }
  },
  mounted() {
    //   this.cek()
  },
  methods: {
    loc(){
        console.log(this.pointTempat)
    },
    cek(checkPoint, centerPoint, km) {
      function arePointsNear(checkPoint, centerPoint, km) {
        var ky = 40000 / 360;
        var kx = Math.cos((Math.PI * centerPoint.lat) / 180.0) * ky;
        var dx = Math.abs(centerPoint.lng - checkPoint.lng) * kx;
        var dy = Math.abs(centerPoint.lat - checkPoint.lat) * ky;
        return Math.sqrt(dx * dx + dy * dy) <= km;
      }
      var point = { lat: this.pointGPS.lat, lng: this.pointGPS.lng };
      var radiuspoint = { lat: this.pointTempat.lat, lng: this.pointTempat.lng };
      console.log()
      var n = arePointsNear(point, radiuspoint, this.circle.radius/1000);
      console.log(n);
      alert(n?"didalam zona":"diluar zona")
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    innerClick() {
      alert('Click!');
    },
  },
};
</script>
